import { useDispatch, useSelector } from 'react-redux';

const About = () => {
    //获取仓库数据
    const { num } = useSelector((state: RootState) => ({
        num: state.num
    }));
    const dispatch = useDispatch();

    const changeNum = () => {
        dispatch({
            type: 'add'
        });
    };
    return <div>
        about{num}
        <button onClick={changeNum}>修改num</button>
    </div>;
}

export default About;